#Scroll {
    position: absolute;
    top: calc(100% - 2.5rem);
    width: 100%;
    height: 100%;
    background-color: white;
    transition: var(--ms);
}

#Scroll:has(#ScrollBtn.up) {
    top: 0;
}

#ScrollBtn {
    cursor: pointer;
    width: 100%;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

#ScrollBtn i {
    pointer-events: none;
    width: 0;
    height: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: calc(var(--ms) * 2);
}

#ScrollBtn.up i {
    transform: rotate(180deg);
}

#Container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: scroll;
    scroll-behavior: smooth;

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
        display: none;
    }
}